* {
    color: #283C4A;
    /*font-family: 'Roboto-Regular', serif;*/
    margin: 0;
    padding: 0;
}

@font-face {
    font-family: 'Roboto-Bold';
    src: local('Roboto-Bold'), url(../fonts/Roboto-Bold.ttf) format('truetype');
}

@font-face {
    font-family: 'Roboto-Medium';
    src: local('Roboto-Medium'), url(../fonts/Roboto-Medium.ttf) format('truetype');
}

@font-face {
    font-family: 'Roboto-Regular';
    src: local('Roboto-Regular'), url(../fonts/Roboto-Regular.ttf) format('truetype');
}

@font-face {
    font-family: 'NotoSerif-Bold';
    src: local('NotoSerif-Bold'), url(../fonts/NotoSerif-Bold.ttf) format('truetype');
}

@font-face {
    font-family: 'NotoSerif-Regular';
    src: local('NotoSerif-Regular'), url(../fonts/NotoSerif-Regular.ttf) format('truetype');
}

@font-face {
    font-family: 'RobotoSlab-Regular';
    src: local('RobotoSlab-Regular'), url(../fonts/RobotoSlab-Regular.ttf) format('truetype');
}

@font-face {
    font-family: 'RobotoSlab-Bold';
    src: local('RobotoSlab-Bold'), url(../fonts/RobotoSlab-Bold.ttf) format('truetype');
}

@font-face {
    font-family: 'RobotoSlab-Medium';
    src: local('RobotoSlab-Medium'), url(../fonts/RobotoSlab-Medium.ttf) format('truetype');
}

.websitetitle {
    font-family: "RobotoSlab-Bold", sans-serif;
}

.aLink {
    font-family: "RobotoSlab-Regular", sans-serif;
}

img {
    max-height: 100%;
    max-width: 100%;
    display: block;
}

/*main-popular-card newsCard  bottom-popular-card  "normal-popular-card */
/*    hl*/
/*    "imgContainer popNewsImgContainer" : "imgContainer"*/
/*        "main-img"  "bottom-img" "nm-img"*/
/*        sub-body ---- image might be in it or outside it*/
/*        txt-bodyContent ---------*/
/*        subtitle  -------and footer maybe in it or outside it*/

/*footerA is row which has 2 divs> "src"    ---    hrs-ago*/
/*footerB is row which has row and div> comments-row 1st-item---comment-img-container(cmntSvgIcon) 2nd item-- comment-txt*/
/*         points*/
.main-img {
    object-fit: contain;
}

.topLeft .imgContainer {
    display: flex;
    justify-content: center;
    background-color: rgba(168, 168, 168, 0.16);

    /*min-width: 300px;*/
}

.topLeft .popNewsImgContainer {
    min-height: 300px;
    max-width: 100%;
    max-height: 350px;
}

.topLeft .popNewsImgContainer img {
    min-width: 70%;
}

.normalCard {
    display: flex;
    flex-direction: column;
    color: red;
}

.last > .newsCard {
    flex: 1;
}

.newsCard {
    color: yellow;
    /*flex: 1;*/

}

.normal-card .imgContainer {
    margin-left: 10px;
}

.imgContainer {
    margin-bottom: 10px;
}

.pop-news .imgContainer {
    margin-bottom: 10px;
}

.hl {
    margin-bottom: 15px;
    /*font-weight: bold;*/
    font-size: 18px;
    font-family: "NotoSerif-Bold", sans-serif;
}

.main-card .hl {
    font-size: 24px;
}

.subtitle {
    margin-bottom: 10px;
    font-family: "NotoSerif-Regular", sans-serif;
    font-size: 13px;
    word-break: break-all;
    hyphens: auto;
}

.footer {
    display: flex;
    width: 100%;
    flex-direction: column;
    font-size: 10px;
    font-family: Roboto-Regular, serif;
}

.src {
    font-size: 11px;
    color: #acb0b4;
}

.footerA, .footerB {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    align-items: center;
}

.comments-row {
    display: flex;
    flex: 1;
    flex-direction: row;
    font-weight: bold;
}

.comment-txt {
    margin-left: 10px;
    display: flex;
    align-items: center;
    filter: contrast(20%);
    letter-spacing: 1px;
}

.comment-img-container {
    padding: 0;
    display: contents;
}

.vertMenuDiv {
    height: 10px;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

.vertMenuDiv > img {
    /*box-sizing: border-box;*/
    position: relative;
    right: -5px;
    margin: 0;
    padding: 0;
    height: 100%;

}

.cmntSvgIcon {
    margin: 0;
    padding: 0;
    filter: contrast(20%);
}


.normal-card .body .imgContainer {
    width: 50%;
    float: right;
}

.normal-card .body .imgWithNoSubtitleContainer {
    width: 100%;
}

.normal-popular-card .body .popNewsImgContainer {
    width: 100%;
}

.popNewsImgContainer {
    margin-left: 0;
    padding-left: 0;
    float: none;
}

.bottom-card .sub-body {
    display: flex;
}

.bottom-card .sub-body > div {
    flex: 1; /*  bottom news card image and other section both have flex 1  */
}

/*mouse icon change if hover over content that is clickable*/
.src:hover, .content:hover, .hl:hover, .comments-row:hover, .subtitle:hover, .imgContainer > img:hover {
    cursor: pointer;
}

.stdCol .newsCard > div:last-child {
    margin-bottom: 15px;
    /*border-bottom: blue solid 2px;*/
    border-bottom: 1px solid #f0f0f0;
}

.stdCol .newsCard:last-child > div:last-child {
    padding-bottom: 0;
    border-bottom: 0;
}

.stdCol .popNewsImgContainer {
    max-height: 200px;
    display: flex;
    background-color: rgba(168, 168, 168, 0.16);
}

.stdCol .popNewsImgContainer img {
    object-fit: contain;
}